<script>
  import Recorder from 'js-audio-recorder'
  let recorder = new Recorder()
  export default {
    name: 'flippage',
    components: {
      'remote-js': {
        render(createElement) {
          return createElement('script', {
            attrs: {
              type: 'text/javascript',
              src: this.src,
              onload: this.onload
            }
          });
        },
        props: {
          src: {
            type: String,
            required: true
          },
        },
      },
    },
    data() {
      return {
        direction: 'left',
        fab: false,
        fling: false,
        hover: false,
        tabs: null,
        top: false,
        right: true,
        bottom: true,
        left: false,
        transition: 'slide-x-reverse-transition',
        dones: {},
        password: 'Password',
        show: false,
        message: '',
        marker: true,
        iconIndex: 0,
        icons: [
          'mdi-emoticon',
          'mdi-emoticon-cool',
          'mdi-emoticon-dead',
          'mdi-emoticon-excited',
          'mdi-emoticon-happy',
          'mdi-emoticon-neutral',
          'mdi-emoticon-sad',
          'mdi-emoticon-tongue',
        ],
      }
    },
    created: function () {
      var that = this;
      this.get_question();
      setTimeout(() => {
        that.$nextTick(function () {
          that.$global2.MathQueue("this_p");
        })
      }, 1000)
    },
    computed: {
      icon() {
        return this.icons[this.iconIndex]
      },
    },

    methods: {
      handleclick() {
        console.log(1)
        recorder.start() // 开始录音
      },
      handleclickl() {
        console.log(2)
        recorder.resume() // 继续录音
      },
      handleclickt() {
        console.log(3)
        recorder.stop() // 结束录音
      },
      handleclickb() {
        console.log(4)
        recorder.play() // 录音播放
      },
      get_question: function () {
        var that = this;
        this.$axios({
          method: "get",
          url: "/api/get_question/",
          params: {
            _id: that.$global.done,
          }
        }).then(function (response) {
          if (response.data.error_num == 0) {
            console.log("错题", response.data);
            that.dones = response.data;
          } else {
            alert("获取失败！");
          }
        })
      },
      toggleMarker() {
        this.marker = !this.marker
      },
      sendMessage() {
        this.resetIcon()
        this.clearMessage()
      },
      clearMessage() {
        this.message = ''
      },
      resetIcon() {
        this.iconIndex = 0
      },
      changeIcon() {
        this.iconIndex === this.icons.length - 1 ?
          this.iconIndex = 0 :
          this.iconIndex++
      },
    },

  }
</script>